<template>
    <el-dialog
            title=""
            :visible.sync="dialogVisible"
            width="70%"
            customClass="flowHelp"
    >
        <el-tabs tab-position="left">
            <el-tab-pane label="">
                <el-divider content-position="left"></el-divider>
                <div></div>
            </el-tab-pane>
            <el-tab-pane label="">
                <el-divider content-position="left"></el-divider>
                <div>
                    
                </div>
                <el-divider content-position="left"></el-divider>
                <pre>this.deleteNode(nodeId)</pre>
            </el-tab-pane>
            <el-tab-pane label="">
                <el-divider content-position="left"></el-divider>
                <div></div>
            </el-tab-pane>
            <el-tab-pane label="">
                <el-divider content-position="left"></el-divider>
                <div>+</div>
            </el-tab-pane>
            <el-tab-pane label="">
                <el-divider content-position="left"></el-divider>
                <div></div>
            </el-tab-pane>
            <el-tab-pane label="">
                <el-divider content-position="left"></el-divider>
                <div>: https://gitee.com/xiaoka2017/easy-flow-sdk</div>
            </el-tab-pane>
        </el-tabs>
    </el-dialog>
</template>

<script>
    export default {
        data() {
            return {
                dialogVisible: false
            }
        },
        components: {},
        methods: {
            init() {
                this.dialogVisible = true
            }
        }
    }
</script>

<style>
    .flowHelp {
        height: 80%;
    }
</style>



