export const easyFlowMixin = {
    data() {
        return {
            jsplumbSetting: {
                // 
                Anchors: ['Top', 'TopCenter', 'TopRight', 'TopLeft', 'Right', 'RightMiddle', 'Bottom', 'BottomCenter', 'BottomRight', 'BottomLeft', 'Left', 'LeftMiddle'],
                // ID
                Container: 'efContainer',
                // :  StateMachineFlowchartBezierStraight
                Connector: ['Bezier', {curviness: 100}],
                // Connector: ['Straight', {stub: 20, gap: 1}],
                // Connector: ['Flowchart', {stub: 30, gap: 1, alwaysRespectStubs: false, midpoint: 0.5, cornerRadius: 10}],
                // Connector: ['StateMachine', {margin: 5, curviness: 10, proximityLimit: 80}],
                // 
                ConnectionsDetachable: false,
                // 
                DeleteEndpointsOnDetach: false,
                /**
                 * 
                 * radius: 
                 */
                // Endpoint: ['Dot', {radius: 5, cssClass: 'ef-dot', hoverClass: 'ef-dot-hover'}],
                /**
                 * 
                 * height: 
                 * width: 
                 */
                // Endpoint: ['Rectangle', {height: 20, width: 20, cssClass: 'ef-rectangle', hoverClass: 'ef-rectangle-hover'}],
                /**
                 * 
                 */
                // Endpoint: ['Image', {src: 'https://www.easyicon.net/api/resizeApi.php?id=1181776&size=32', cssClass: 'ef-img', hoverClass: 'ef-img-hover'}],
                /**
                 * 
                 */
                Endpoint: ['Blank', {Overlays: ''}],
                // Endpoints: [['Dot', {radius: 5, cssClass: 'ef-dot', hoverClass: 'ef-dot-hover'}], ['Rectangle', {height: 20, width: 20, cssClass: 'ef-rectangle', hoverClass: 'ef-rectangle-hover'}]],
                /**
                 * 
                 * fill: #12aabb
                 * outlineWidth: 
                 */
                EndpointStyle: {fill: '#1879ffa1', outlineWidth: 1},
                // jsPlumb
                LogEnabled: true,
                /**
                 * 
                 */
                PaintStyle: {
                    // 
                    stroke: '#E0E3E7',
                    // 
                    strokeWidth: 1,
                    //  https://blog.csdn.net/roymno2/article/details/72717101
                    outlineStroke: 'transparent',
                    // 
                    outlineWidth: 10
                },
                DragOptions: {cursor: 'pointer', zIndex: 2000},
                /**
                 *    https://www.jianshu.com/p/d9e9918fd928
                 */
                Overlays: [
                    // 
                    ['Arrow', {
                        width: 10, // 
                        length: 8, // 
                        location: 1, // 01
                        direction: 1, // 1-1
                        foldback: 0.623 // 0.6231
                    }],
                    // ['Diamond', {
                    //     events: {
                    //         dblclick: function (diamondOverlay, originalEvent) {
                    //             console.log('double click on diamond overlay for : ' + diamondOverlay.component)
                    //         }
                    //     }
                    // }],
                    ['Label', {
                        label: '',
                        location: 0.1,
                        cssClass: 'aLabel'
                    }]
                ],
                //  svgcanvas
                RenderMode: 'svg',
                // 
                HoverPaintStyle: {stroke: '#b0b2b5', strokeWidth: 1},
                // 
                // EndpointHoverStyle: {fill: 'red'}
                Scope: 'jsPlumb_DefaultScope' // scope
            },
            /**
             * 
             */
            jsplumbConnectOptions: {
                isSource: true,
                isTarget: true,
                // 4 ContinuousAutoDefault
                anchor: 'Continuous',
                // label
                labelStyle: {
                    cssClass: 'flowLabel'
                },
                // jsplumb label style
                emptyLabelStyle: {
                    cssClass: 'emptyFlowLabel'
                }
            },
            /**
             * 
             */
            jsplumbSourceOptions: {
                // DOM
                filter: '.flow-node-drag',
                filterExclude: false,
                anchor: 'Continuous',
                // 
                allowLoopback: true,
                maxConnections: -1,
                onMaxConnections: function (info, e) {
                    console.log(`: ${info.maxConnections}`)
                }
            },
            //  https://www.cnblogs.com/mq0036/p/7942139.html
            jsplumbSourceOptions2: {
                // DOM
                filter: '.flow-node-drag',
                filterExclude: false,
                // anchor: 'Continuous',
                // 
                allowLoopback: true,
                connector: ['Flowchart', {curviness: 50}],
                connectorStyle: {
                    // 
                    stroke: 'red',
                    // 
                    strokeWidth: 1,
                    //  https://blog.csdn.net/roymno2/article/details/72717101
                    outlineStroke: 'transparent',
                    // 
                    outlineWidth: 10
                },
                connectorHoverStyle: {stroke: 'red', strokeWidth: 2}
            },
            jsplumbTargetOptions: {
                // DOM
                filter: '.flow-node-drag',
                filterExclude: false,
                // 
                anchor: 'Continuous',
                allowLoopback: true,
                dropOptions: {hoverClass: 'ef-drop-hover'}
            }
        }
    }
}



