var dataE = {
    name: 'E',
    nodeList: [
        {
            id: 'nodeA',
            name: 'D-A',
            type: 'task',
            ico: 'el-icon-user-solid',
            state: 'success'
        },
        {
            id: 'nodeB',
            type: 'task',
            name: 'D-B',
            ico: 'el-icon-goods',
            state: 'error'
        },
        {
            id: 'nodeC',
            name: 'D-C',
            type: 'task',
            ico: 'el-icon-present',
            state: 'warning'
        }, {
            id: 'nodeD',
            name: 'D-D',
            type: 'task',
            ico: 'el-icon-present',
            state: 'running'
        }
    ],
    lineList: [{
        from: 'nodeA',
        to: 'nodeB'
    }, {
        from: 'nodeA',
        to: 'nodeC',
        label: 'hello'
    }, {
        from: 'nodeB',
        to: 'nodeD'
    }, {
        from: 'nodeC',
        to: 'nodeD'
    }, {
        from: 'nodeC',
        to: 'nodeC'
    }
    ]
}

export function getDataE() {
    return dataE
}



